
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0 auto;
  font-family: "Helvetica", "Arial", sans-serif;
  max-width: 50em;
  padding: 4em 1em;
  line-height: 1.5;
}
#app {
  margin-top: 10px;
  margin-bottom: 30px;
}
h1{
    color: #333;
}
.light{
    display: inline-block;
    font-size: 70px;
    opacity: 0.5;
}
.bold{
    display: inline-block;
    font-size: 70px;
}
.tag-line{
    margin-top: 20px;
    margin-left: 40px;
    font-size: 20px;
    opacity: 0.7;
}
a{
    color: #000;
    cursor: pointer;
}
a:hover{
    color: #0090d3;
    text-decoration: underline;
}
input{
    display: block;
    margin-bottom: 20px;
    padding: 10px;
}
button{
    background: none;
    border: none;
    background-color: #0090d3;
    color: #fff;
    padding: 10px;
    cursor: pointer;
    margin-right: 10px;
}
#notification{
    height: 50px;
    width: 400px;
    margin-top: 20px;
    margin-left: 40px;
    padding: 10px;
    color: #fff;
}
#notification .msg{
    margin: 15px;
    float: left;
}
#notification .ignore{
    margin: 15px;
    float: right;
    cursor: pointer;
}
.error{
    background: #ff0000;
}
.success{
    background: #23C15D;
}



